import ReactDOM from 'react-dom/client';
import './style.css';
import React from 'react';



const config = require("./config.json")
// const rohanApiEndpoint = config.apiEndpoint
// const localEndpoint = config.localApiEndpoint
const myApiEndpoint = config.myApiEndpoint


const root = ReactDOM.createRoot(document.getElementById('root'));


const Watch = (props) => {
  // console.log(props.item.list)
  // console.log(props.item)
  // console.log(props.props)
  const { name, imageUrl, id, description } = props.details
  // console.log(name)
  return (
    <article className='watch'>
      <img src={imageUrl} alt={name} />
      <h2>{name}</h2>
      <p>{description}</p>

    </article>
  )
};

const WatchList = (list) => {
  // console.log(list.list)
  return (
    <section className='watchList'>
      {
        React.Children.toArray(
          list.list.map(
            (anObject) => {
              return <Watch details={anObject} />
            }
          )
        )
      }
    </section>
  )
};


