try {
  const response = await fetch(myApiEndpoint)

  if (!response.ok) {
    //error data is object {detail: "bla....bla.."}
    const errorData = await response.json().catch(() => null); // catch Handle cases where response is not JSON
    console.error("Error details:", response.status, errorData);
    throw new Error(`HTTP error! Status: ${response.status} -- message: ${errorData.detail}`);

  }

  const object = await response.json();
  root.render(<WatchList list={object.data} />)

} catch (error) {
  console.log(error.message)
  console.error("problem ayo kya: ", error)

  root.render(
    <>
      <h2>There seems to be some problem while fetching API</h2>
      <p>{error.message}</p>
    </>
  )
}